/*STYLE RESET START*/
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  color: #333;
  font: 300 14px "Open Sans", sans-serif;
  background-color: #f5f5f5;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  max-width: 100%;
}

textarea {
  resize: vertical;
}

label {
  margin: 0;
}

input, button, select {
  border: none;
  outline: none;
}

button {
  cursor: pointer;
}

/*STYLE RESET END*/
.container-custom {
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}

.container-footer {
  max-width: 1170px;
  padding: 0 20px;
  margin: 0 auto;
  height: 100%;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.column {
  flex-direction: column;
}

header {
  padding-top: 40px;
  position: absolute;
  width: 100%;
  z-index: 1;
  color: white;
}

header .logo {
  height: 52px;
  font-size: 36px;
  text-transform: uppercase;
  align-self: flex-start;
}

header .logo .first-part {
  width: 52px;
  font-weight: 700;
  background-color: white;
  color: #4a4c55;
  text-align: center;
  border: 3px solid white;
}

header .logo .second-part {
  padding: 0 10px;
  font-weight: 300;
  border: 3px solid white;
}

header nav {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

header nav li a {
  display: flex;
  align-items: center;
  height: 52px;
  transition: all .2s;
  border-bottom: 1px solid transparent;
}

header nav li a:hover {
  color: #e74c3c;
  border-bottom: 1px solid #e74c3c;
}

header nav li + li {
  margin-left: 55px;
}

@media (max-width: 992px) {
  header nav li a {
    height: unset;
  }
  header nav li + li {
    margin-left: 0;
  }
}

@media (max-width: 480px) {
  header {
    padding-top: 20px;
  }
  header .logo {
    height: 33px;
    font-size: 26px;
  }
  header .logo .first-part {
    width: 33px;
  }
  header .logo .second-part {
    padding: 0 5px;
  }
}

button.btn {
  font-weight: 700;
  color: white;
  text-transform: uppercase;
  padding: 15px 0;
  width: 222px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

button.btn.btn-default {
  border: 1px solid white;
  background-color: transparent;
}

button.btn.btn-default:hover {
  background-color: #e74c3c;
  color: white;
}

@media (max-width: 992px) {
  button.btn {
    padding: 10px 0;
    max-width: 150px;
    font-size: 12px;
  }
}

@media (max-width: 600px) {
  button.btn {
    padding: 8px 0;
    max-width: 130px;
    font-size: 11px;
  }
}

@media (max-width: 480px) {
  button.btn {
    max-width: 100px;
    font-size: 10px;
  }
}

.slicknav_menu {
  display: none;
}

@media (max-width: 992px) {
  #header-nav {
    display: none;
  }
  .slicknav_menu {
    display: block;
  }
  .slicknav_menu .slicknav_nav a {
    padding: 10px 25px;
  }
}

@media (max-width: 480px) {
  .slicknav_menu {
    padding: 0;
    border-radius: 4px;
  }
  .slicknav_menu .slicknav_btn {
    margin: 0;
  }
}

.my-fa-facebook-f:before {
  content: "\f39e";
}

.my-fa-twitter:before {
  content: "\f099";
}

.my-fa-rss:before {
  content: "\f09e";
}

.my-fa-google-plus-g:before {
  content: "\f0d5";
}

.my-fa-linkedin-in:before {
  content: "\f0e1";
}

.my-fa-skype:before {
  content: "\f17e";
}

.my-fa-vimeo-v:before {
  content: "\f27d";
}

.my-fa-tumblr:before {
  content: "\f173";
}

.my-fa-chevron-up:before {
  content: "\f077";
}

.anchor {
  opacity: 0;
  width: 50px;
  height: 50px;
  position: fixed;
  right: 3%;
  bottom: 10%;
  border-radius: 50%;
  transition: .2s ease-out;
  background-color: #e74c3c;
  text-align: center;
  z-index: 1;
}

.anchor i {
  color: white;
  line-height: 50px;
}

.anchor.show {
  opacity: 1;
}

section.main-slider {
  position: relative;
  height: 930px;
}

section.main-slider .background {
  background: url("../img/main-bg.jpg") no-repeat center;
  background-size: cover;
  position: absolute;
  width: 100%;
  height: 100%;
}

section.main-slider .background:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: .7;
}

section.main-slider .carousel-wrap {
  padding: 320px 10% 0;
  text-align: center;
}

section.main-slider .carousel-wrap p {
  max-width: 585px;
  display: inline-block;
}

section.main-slider .carousel-wrap .carousel-arrow {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 40px;
  background-color: #6f6863;
  cursor: pointer;
  border-radius: 2px;
  transition: .2s ease-out;
  text-align: center;
}

section.main-slider .carousel-wrap .carousel-arrow i {
  color: #3c3530;
  font-size: 20px;
  line-height: 40px;
  width: 100%;
  transition: .2s ease-out;
}

section.main-slider .carousel-wrap .carousel-arrow.lg:hover {
  background-color: #e74c3c;
}

section.main-slider .carousel-wrap .carousel-arrow.lg:hover i {
  color: #fff;
}

section.main-slider .carousel-wrap .carousel-arrow.mobile {
  display: none;
}

section.main-slider .carousel-wrap .carousel-arrow.left {
  left: 3%;
}

section.main-slider .carousel-wrap .carousel-arrow.right {
  right: 3%;
}

section.main-slider .carousel-wrap #carousel .carousel-item {
  color: #f4f5f9;
  position: relative;
}

section.main-slider .carousel-wrap #carousel .carousel-item h1 {
  font-weight: 700;
  font-size: 72px;
  text-transform: uppercase;
  margin-top: 35px;
}

section.main-slider .carousel-wrap #carousel .carousel-item h2 {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 24px;
}

section.main-slider .carousel-wrap #carousel .carousel-item p {
  display: block;
  margin: 0 auto;
  color: #cbc6c0;
}

section.main-slider .carousel-wrap #carousel .carousel-item .underline {
  display: inline-block;
  border: 4px solid #e74c3c;
  border-radius: 50%;
  position: relative;
  margin: 30px 0 35px;
}

section.main-slider .carousel-wrap #carousel .carousel-item .underline:before {
  content: '';
  border-bottom: 1px solid white;
  width: 80px;
  position: absolute;
  left: -105px;
}

section.main-slider .carousel-wrap #carousel .carousel-item .underline:after {
  content: '';
  border-bottom: 1px solid white;
  width: 80px;
  position: absolute;
  right: -105px;
}

section.main-slider .carousel-wrap #carousel .carousel-item .btn-block {
  margin-top: 60px;
}

section.main-slider .carousel-wrap #carousel .carousel-item .btn-block button + button {
  margin-left: 30px;
}

@media (max-width: 992px) {
  section.main-slider {
    height: 830px;
  }
  section.main-slider .carousel-wrap {
    padding: 280px 15% 0;
  }
  section.main-slider .carousel-wrap .carousel-arrow.lg:hover {
    display: none;
  }
  section.main-slider .carousel-wrap .carousel-arrow.mobile {
    display: block;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item h1 {
    font-size: 42px;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item h2 {
    font-size: 20px;
  }
}

@media (max-width: 600px) {
  section.main-slider {
    height: 730px;
  }
  section.main-slider .carousel-wrap {
    padding: 240px 15% 0;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item h1 {
    font-size: 32px;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item h2 {
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  section.main-slider {
    height: 630px;
  }
  section.main-slider .carousel-wrap {
    padding: 160px 15% 0;
  }
  section.main-slider .carousel-wrap .carousel-arrow {
    width: 25px;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item h1 {
    font-size: 32px;
    margin-top: 25px;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item h2 {
    font-size: 18px;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item .underline {
    margin: 20px 0 20px;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item p {
    font-size: 12px;
  }
  section.main-slider .carousel-wrap #carousel .carousel-item .btn-block button + button {
    margin-left: 5px;
  }
}

section.info-blocks .block-square {
  width: 50%;
}

section.info-blocks .block-square .item {
  width: 50%;
}

section.info-blocks .block-square .item.text-block {
  color: white;
  padding: 5%;
  position: relative;
}

section.info-blocks .block-square .item.text-block .red-arrow {
  position: absolute;
  border: 30px solid transparent;
  top: 50%;
  transform: translateY(-50%);
}

section.info-blocks .block-square .item.text-block .red-arrow.right {
  border-left: 25px solid #e74c3c;
  right: -55px;
}

section.info-blocks .block-square .item.text-block .red-arrow.left {
  border-right: 25px solid #e74c3c;
  left: -55px;
}

section.info-blocks .block-square .item.text-block h3 {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 24px;
  position: relative;
  margin: 15px 0 30px;
}

section.info-blocks .block-square .item.text-block h3:after {
  position: absolute;
  content: '';
  background-color: white;
  width: 40px;
  height: 2px;
  left: 0;
  top: 40px;
}

section.info-blocks .block-square .item.text-block p {
  font-size: 16px;
  overflow: hidden;
  position: relative;
  line-height: 1.5em;
  max-height: 10.5em;
  text-align: justify;
  margin-right: -1em;
  padding-right: 1em;
}

section.info-blocks .block-square .item.text-block p:before {
  content: '...';
  position: absolute;
  right: 0;
  bottom: 0;
}

section.info-blocks .block-square .item.text-block p:after {
  content: '';
  position: absolute;
  right: 0;
  width: 1em;
  height: 1.5em;
  margin-top: 0.2em;
  background: #e74c3c;
}

section.info-blocks .block-square .item.text-block a {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 600;
  font-style: italic;
  font-size: 18px;
}

section.info-blocks .block-square .item .item-text-wrap {
  height: 100%;
}

section.info-blocks .block-square .text-block {
  background-color: #e74c3c;
}

@media (max-width: 1600px) and (min-width: 993px) {
  section.info-blocks .block-square .item.text-block h3 {
    margin: 15px 0 25px;
  }
  section.info-blocks .block-square .item.text-block p {
    font-size: 14px;
    max-height: 5.6em;
    line-height: 1.4em;
  }
}

@media (max-width: 992px) {
  section.info-blocks {
    flex-direction: column;
  }
  section.info-blocks .block-square {
    width: 100%;
  }
}

@media (max-width: 768px) and (min-width: 481px) {
  section.info-blocks .block-square .item.text-block h3 {
    margin: 10px 0 20px;
  }
  section.info-blocks .block-square .item.text-block p {
    font-size: 14px;
    max-height: 5.6em;
    line-height: 1.4em;
  }
}

@media (max-width: 480px) {
  section.info-blocks .block-square {
    flex-direction: column;
  }
  section.info-blocks .block-square .item {
    width: 100%;
    height: 100vw;
  }
  section.info-blocks .block-square .item:nth-child(3n) {
    order: 1;
  }
  section.info-blocks .block-square .item.text-block .red-arrow {
    position: absolute;
    border: 30px solid transparent;
    top: -55px;
  }
  section.info-blocks .block-square .item.text-block .red-arrow.right {
    border-left: 30px solid transparent;
    right: unset;
  }
  section.info-blocks .block-square .item.text-block .red-arrow.left {
    border-right: 30px solid transparent;
    left: unset;
  }
  section.info-blocks .block-square .item.text-block .red-arrow.up {
    border-bottom: 25px solid #e74c3c;
    left: 50%;
    transform: translateX(-50%);
  }
}

section.trial {
  height: 480px;
  color: white;
  position: relative;
  text-align: center;
}

section.trial .trial-background {
  background: url("../img/footer-bg.jpg");
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: -1;
}

section.trial .trial-background:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  background-color: #000;
  opacity: .6;
}

section.trial .trial-title {
  font-weight: 700;
  font-size: 36px;
}

section.trial .trial-text {
  margin-top: 15px;
  margin-bottom: 65px;
  font-size: 18px;
}

@media (max-width: 992px) {
  section.trial .trial-title {
    font-size: 30px;
  }
  section.trial .trial-text {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  section.trial .trial-title {
    font-size: 26px;
  }
  section.trial .trial-text {
    font-size: 14px;
  }
}

footer {
  height: 90px;
  background-color: #202020;
  color: #999999;
}

footer .social-nav {
  font-size: 18px;
}

footer .social-nav li i {
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
}

footer .social-nav li + li {
  margin-left: 15px;
}

footer .social-nav li:hover {
  color: #e74c3c;
}

footer span {
  font-weight: 600;
}

@media (max-width: 768px) {
  footer .footer-wrap {
    flex-direction: column;
    padding: 10px 0;
  }
}

@media (max-width: 480px) {
  footer .social-nav {
    font-size: 16px;
  }
  footer .social-nav li + li {
    margin-left: 5px;
  }
  footer span {
    font-size: 12px;
  }
}
